import React from 'react';
import { Link, useNavigate } from 'react-router-dom';
import * as ROUTES from '../../constants/routes';
import './signOut.scss';

import HeroBanner from '../../components/HeroBanner/HeroBanner';
import Header from '../../components/Header/Header';
import Logo from '../../components/Logo/Logo';
import Footer from '../../components/Footer/Footer';
import FooterData from '../../components/Footer/Footer.json';
import { useEffect } from 'react/cjs/react.development';

function SignOut(props) {
  const navigate = useNavigate();

  useEffect(() => {
    const id = setTimeout(() => {
      navigate(process.env.PUBLIC_URL + ROUTES.HOME);
    }, 15000);

    return () => {
      clearTimeout(id);
    };
  });

  return (
    <HeroBanner bg="bg-signout">
      <div className="signOut text-white">
        <Header>
          <div className="signOut__head flex">
            <Logo state="clickable" route="home" />
          </div>
        </Header>
        <main className="signOut__card">
          <div className="signOut__card__content bg-white text-grey">
            <h1 className="fw-400">Vous partez dj ?</h1>
            <p>
              Le saviez-vous ? Inutile de vous dconnecter de Netflix
              systmatiquement. Vous devez le faire uniquement si vous tes sur
              un ordinateur partag ou public.
              <br />
              <br />
              Vous serez redirig vers Netflix.com dans 15 secondes
            </p>
            <Link
              to={process.env.PUBLIC_URL + ROUTES.HOME}
              className="btnToHome text-white"
            >
              Essayer maintenant
            </Link>
          </div>
        </main>

        <Footer bg="rgba(0,0,0,0.8)">
          <Footer.Title>
            Des quesions ? Appelez le (+33)0805-543-063
          </Footer.Title>
          <Footer.Grid gridItems={FooterData.sign} />
        </Footer>
      </div>
    </HeroBanner>
  );
}

export default SignOut;
