import React from 'react';
import './SubForm.scss';

function SubForm({ num }) {
  return (
    <form className="subForm">
      <h3 className="fs-600 fw-400">
        Prt  regarder Netflix ? Saisissez votre adresse e-mail pour vous
        abonner ou ractiver votre abonnement
      </h3>
      <div className="subForm__block flex">
        <label htmlFor={`email-address${num}`} className="sr-only">
          Saisir l'addresse e-mail
        </label>
        <input
          type="text"
          id={`email-address${num}`}
          placeholder="Adresse e-mail"
          className="fs-400"
        ></input>
        <button type="submit" className="fs-500 btn ">
          Commencer
        </button>
      </div>
    </form>
  );
}

export default SubForm;
